body {
  font-family: "Cabin", sans-serif;
  font-size: 1.8rem;
  display: flex;
  flex-wrap: wrap;
  min-height: 100vh;
  flex-direction: column;
  gap: 3.4rem;
  background: rgb(178, 189, 184);
  background: linear-gradient(0deg, rgba(178, 189, 184, 0.7568277311) 0%, rgba(164, 163, 163, 0.7904411765) 68%);
}

a {
  text-decoration: none;
  color: #393939;
}

img {
  width: 100%;
  height: auto;
}

h1 {
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h2 {
  font-size: 2.4rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h3 {
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

p {
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.flex-col-separacion-10 {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.flex-col-separacion-20 {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.text-derecha {
  text-align: end;
}

.text-center {
  text-align: center;
}

.boton {
  background-color: #88C34A;
  padding: 1.5rem 4rem;
  color: #393939;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  max-width: 100%;
  display: inline-block;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.boton:hover {
  background-color: #6ea436;
  cursor: pointer;
}

.boton--eliminar {
  background-color: #8b1212;
  padding: 1.5rem 4rem;
  color: #e2ded8;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  max-width: 100%;
  display: inline-block;
}
.boton--eliminar:hover {
  background-color: #5e0c0c;
  cursor: pointer;
}

.botonera {
  display: flex;
  flex-direction: column;
}

.botonera--vertical {
  display: flex;
  flex-direction: column;
}
.botonera--vertical .boton {
  width: 95%;
}

.botonera--horizontal2 {
  gap: 1rem;
  display: flex;
  flex-direction: row;
}
.botonera--horizontal2 .boton {
  flex-basis: 50%;
  margin: 0;
}

.boton--ancho {
  width: 100%;
  margin: 0;
}

.card {
  margin: 0 auto;
  border-radius: 1rem;
  border: 2px solid #ECE4D8;
  width: 95%;
  background-color: #e2ded8;
  padding: 2rem 1rem;
}
@media (min-width: 1200px) {
  .card {
    width: 70%;
  }
}

.contenedor {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .contenedor {
    flex-direction: row;
    width: 80%;
  }
}
@media (min-width: 1200px) {
  .contenedor {
    width: 75%;
  }
}

.admin-imagen {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.admin-imagen div {
  padding: 0.5rem;
  flex-basis: calc(50% - 1rem);
}
.admin-imagen div .boton {
  width: 90%;
}
.admin-imagen img {
  padding: 1rem;
  max-height: 100%;
  max-width: 100%;
  flex-basis: calc(50% - 1rem);
}

form fieldset {
  border-radius: 0.5rem;
  margin: 1rem;
  display: block;
}

.entrada {
  margin: 1rem 0;
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
}
.entrada input {
  padding: 2rem 4rem;
  background-color: #d0cecc;
  border: none;
  border-radius: 0.5rem;
}

#form-fecha fieldset {
  border: none;
}

.radio div {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.alerta-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.alerta-form .alerta {
  text-transform: uppercase;
  padding: 1rem 1rem;
  border-radius: 0.5rem;
}
.alerta-form .error {
  background-color: #8b1212;
  color: #e2ded8;
  font-weight: 700;
}
.alerta-form .exito {
  background-color: #02db02;
  color: #393939;
  font-weight: 700;
}

#mapa .avaiable {
  fill: #c3e6d7;
  cursor: pointer;
}
#mapa .avaiable:hover {
  fill: #9fd7bf;
}
#mapa .selected {
  fill: #62b7bc;
  cursor: pointer;
}
#mapa .reserved {
  fill: #8b1212;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Aquí puedes ajustar la transparencia cambiando el valor del canal alfa (0.5 en este caso) */
  z-index: 2; /* Asegúrate de que el valor del z-index sea superior al del resto del contenido de la página para que el overlay esté en primer plano */
  display: flex;
}
.overlay .card {
  overflow: auto;
  margin: 1rem auto;
  position: relative;
  padding: 1rem;
  height: auto;
}
.overlay .card h3 {
  margin-top: 0;
  width: 85%;
}
.overlay .card img {
  border-radius: 0.5rem;
}
.overlay .pax {
  font-weight: 700;
  font-size: 2rem;
}
.overlay .price {
  display: inline;
  font-weight: 700;
  font-size: 2rem;
}
.overlay span {
  font-weight: 400;
  font-size: 1.6rem;
}
.overlay .cerrar {
  position: absolute;
  right: 2.7rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.overlay .cerrar svg {
  color: #393939;
  border: 2px solid #393939;
  border-radius: 0.5rem;
}
.overlay .boton {
  width: 100%;
  margin: 0;
}

.div-imagen {
  margin: 2rem auto;
  position: relative;
}
.div-imagen .siguiente-foto {
  cursor: pointer;
  position: absolute;
  width: 2rem;
  height: 2rem;
  right: 1rem;
  top: calc(50% - 1.8rem);
  background-color: rgba(226, 222, 216, 0.7);
}
.div-imagen .siguiente-foto svg {
  height: 2rem;
  width: 2rem;
}
.div-imagen .anterior-foto {
  cursor: pointer;
  position: absolute;
  width: 2rem;
  height: 2rem;
  left: 1rem;
  top: calc(50% - 1.8rem);
  background-color: rgba(226, 222, 216, 0.7);
}
.div-imagen .anterior-foto svg {
  height: 2rem;
  width: 2rem;
}

.no-foto {
  background-color: #d0cecc;
  padding: 10rem 2rem;
  margin: 2rem auto;
  text-align: center;
  border: 1px solid #8b1212;
  border-radius: 0.5rem;
}

.no-scroll {
  overflow: hidden;
}

header {
  width: 100%;
  margin: 0 auto;
  border-radius: 0;
  border: 2px solid #ECE4D8;
  background-color: #C3E6D7;
}

.navegacion {
  margin: 0 auto;
  width: 90%;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navegacion h1 {
  margin: auto 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .navegacion .user {
    grid-column: 2/3;
    grid-row: 3/4;
    justify-self: end;
  }
  .navegacion .user p {
    font-size: 1.2rem;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .navegacion h1 {
    margin: 0;
  }
  .navegacion nav {
    grid-column: 2/3;
  }
}

nav {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  nav {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  nav {
    align-items: center;
  }
}
nav ul {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  nav ul {
    flex-direction: row;
    gap: 0rem;
  }
}
@media (min-width: 1024px) {
  nav ul {
    gap: 1rem;
  }
}
nav li {
  border-radius: 0.2rem;
  display: flex;
  justify-content: center;
}
nav li a {
  padding: 2.5rem 1rem;
}
@media (min-width: 1024px) {
  nav li a {
    padding: 2.5rem 2rem;
  }
}
nav li:hover {
  background-color: #88C34A;
}

.navegacion__boton {
  display: flex;
  margin: 2rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .navegacion__boton {
    justify-content: end;
  }
}
@media (min-width: 1024px) {
  .navegacion__boton {
    display: none;
  }
}

.navegacion__boton--mostrar {
  display: block;
  height: 5rem;
  width: 5rem;
}

.navegacion__boton--cerrar {
  display: none;
  height: 5rem;
  width: 5rem;
}

.navegacion__enlaces {
  display: none;
}
@media (min-width: 1024px) {
  .navegacion__enlaces {
    display: flex;
  }
}

footer {
  width: 100%;
  margin: 0 auto -6rem 0;
  border-radius: 0;
  border: 2px solid #ECE4D8;
  background-color: #C3E6D7;
}
footer div {
  width: 90%;
  margin: 0 auto;
}

.reserva {
  padding: 2rem;
  border-bottom: 1px solid #393939;
}
.reserva:first-of-type {
  padding-top: 0;
}
.reserva:last-of-type {
  padding-bottom: 0;
  border: none;
}

p span {
  font-weight: 700;
}

.card-izquierda {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .card-izquierda {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.card-izquierda a {
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .card-izquierda a {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}

.card-izquierda__texto {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: space-between;
}
.card-izquierda__texto h3 {
  text-align: center;
  margin-top: 4rem;
}
.card-izquierda__texto p {
  text-align: justify;
}

.card-izquierda__imagen {
  grid-row: 1/4;
}
.card-izquierda__imagen img {
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .card-izquierda__imagen img {
    height: 100%;
    object-fit: cover;
  }
}

.card-derecha {
  padding: 2rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .card-derecha {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .card-derecha a {
    grid-row: 3/4;
  }
}

.card-derecha__imagen img {
  width: 100%;
  height: 30rem;
  object-fit: cover;
  object-position: 50% 70%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .card-derecha__imagen img {
    height: 40rem;
  }
}
@media (min-width: 768px) {
  .card-derecha__imagen {
    grid-column: 2/3;
    grid-row: 1/4;
  }
}

.card-derecha__texto h3 {
  text-align: center;
}
.card-derecha__texto p {
  text-align: justify;
}

@media (min-width: 1024px) {
  .acommodation main {
    order: 1;
  }
}
@media (min-width: 1024px) {
  .acommodation section {
    order: 2;
  }
}
.acommodation .acommodation__imagen img {
  display: none;
  border-radius: 0.5rem;
}
@media (min-width: 1024px) {
  .acommodation .acommodation__imagen img {
    display: block;
    height: 71rem;
  }
}

fieldset #mapa {
  display: flex;
  justify-content: center;
}
fieldset #mapa svg {
  margin-top: 2rem;
}